.footer {
  background-color: var(--container-color);
  border-top: 1px solid rgba (0, 0, 0, 0.1);
}

.footer_container {
	padding: 2rem 0 6rem;
}


.footer_title,
.footer_link {
	color: var(--title-color);
}

.footer_title {
	text-align: center;
	margin-bottom: var(--mb-2);
}

.footer_link:hover {
	color: var(--title-color-dark);
}

.footer_list {
	display: flex;
	justify-content: center;
	column-gap: 1.5rem;
	margin-bottom: var(--mb-2);
}

.footer_copy {
	display: block;
	margin-top: 4.5rem;
	color: var(--title-color);
	text-align: center;
	font-size: var(--smaller-font-size);
}
